<template>
</template>
<script>
	import { Toast } from 'vant';
	export default {
		data() {
			return {
			}
		},
		methods: {
		},
		watch: {
			'$route' (to, from) {
				var that = this;
				that.$dialog.alert({
					message: '请先登录APP',
					confirmButtonText: '我已登录'
				}).then(() => {
					that.$router.replace(that.util.getUrl({path: '/pages/member/mine'}));
				});
			}
		},
		mounted() {
			var that = this;
			function QFH5ready() {
				Toast.loading({
					message: '获取登陆�..',
					forbidClick: true,
				});
				setTimeout(function(){
					Toast.clear();
					QFH5.getUserInfo(function(state, data){
						if(state == 1){
							var params = data;
							params.forward = that.util.getStorage('forwardUrl');
							that.util.request({
								method: 'post',
								url: 'wmall/auth/qianfan',
								data: params
							}).then((res) => {
								let result = res.data.message;
								if(result.errno != 0) {
									that.util.$toast(result.message);
									QFH5.jumpLogin(function(state,data){});
									QFH5.jumpLogin(function(state,data){
									});
									return false;
								} else {
									result = result.message;
									if(result.member) {
                                	    that.util.setStorage('itoken', result.member.token);
									}
									location.href = result.url;
								}
							})
						} else {
							QFH5.jumpLogin(function(state, data){
							});
						}
					});
				}, 1000);
			}
			QFH5ready();
		}
	}
</script>


